<template>
  <div class="l-pigList">
    <van-nav-bar 
      title="购物车"
      left-arrow 
      @click-left="onClickLeft" 
      class="c-nav-bar"
    >
    </van-nav-bar>
    <ul class="l-body c-content">
      <li v-for="item in goodList" class="l-list" :key="item.goodsid">
        <div class="l-img"><img :src="item.pic_url" @error.once="setDefImg"/></div>
        <div class="l-imgInfo">
          <div class="l-img-title">
            <span>{{item.name}}</span>
            <span class="l-level"><img :src="require('../../assets/icon/level' + item.goodsid +'.jpg')"></span>
          </div>
          <div class="l-img-price">
            <span>价格:</span><span class="l-img-marginL">{{item.price}}</span><span>xtoken</span>
          </div>
          <div class="l-img-memo">
            <span>产能:</span><span class="l-img-marginL">{{item.memo}}</span>
          </div>
        </div>
        <div class="l-num">
          <span v-show="item.count > 0" @click="reduce(item)"><img :src="require('../../assets/icon/reduce.png')"></span>
          <span v-show="item.count > 0">{{item.count}}</span>
          <span @click="add(item)"><img :src="require('../../assets/icon/add.png')"></span>
        </div>
      </li>
    </ul>
    <div class="l-list-footer">
      <div class="l-footer-img"><img :src="require('../../assets/icon/coin.png')"></div>
      <div class="l-footer-right">
        <div class="l-footer-amount"><span>{{amount}}</span>xtoken</div>
        <div class="l-footer-balance" :class="{disabled: theLink==''}" @click="onPay">支付</div>
      </div>
    </div>
    <page-loading v-show="loading"></page-loading>
    <van-actionsheet v-model="payShow" title="请输入密码" @cancel="onCanelPay">
      <Pay @finish="onPwdFinish" :num="total"></Pay>
    </van-actionsheet>    
  </div>
</template>
<script>
import '../../common/common.scss'
import '../../common/shop.scss'
import util from '../../common/util'
import Shop from './Shop'
import Pay from '@/components/Pay'
export default {
  name: 'cart',
  mixins: [Shop],
  components: {
    Pay
  },
  data() {
    return {
      payShow: false,
      total: 0
    };
  },

  methods: {
    setGoodList(list) {
      list.forEach((obj) => {
        obj.count = this.cartDict[obj.goodsid] ? this.cartDict[obj.goodsid].count : 0
      })
      this.filterGoodlist(list);
    },    
    filterGoodlist(aList) {
      this.goodList = aList.filter((item) => {
        return item.count > 0;
      })
    },
    reduce (item) {
      if(item.count == 1) {
        this.$dialog.confirm({
          title: '提示',
          message: '确定要删除该商品?'
        }).then(() => {
          this.doReduce(item)
        }).catch(() => {
          console.log("cancel");
        });
      }
      else {
        this.doReduce(item)
      }
    },
    doReduce(item) {
      this.loading = true;
      this.$http.post("order/reduceCart", {
        "goodsid": item.goodsid
      }).then((res) => {
        item.count--;
        this.filterGoodlist(this.goodList);
      }).catch((e) => {
        console.log(e)
      }).finally(() => {
        this.loading = false;
      });       
    },
    onPay() {
      let arr = this.goodList.map((item) => {
        return {
          goodsid: item.goodsid,
          count: item.count
        }
      })
      if(!arr.length) {
        return;
      }
      this.payShow = true;
      this.total = this.getTotal();
      // this.loading = true;
      // //提交订单 密码为空 即提交了一个未完成的订单
      // this.$http.post("order/confirmCart", {
      //   paypasswd: "123456",
      //   "did":"device id",
      // }).then((res)=>{
      //   console.log(res)
      //   //this.$router.push("/shop/pay");
      //   //todo 现在不知道怎么才能提交一个未完成的订单
      //   //this.payShow = true;
      //  // this.goods = arr;
      // }).catch((e) => {
      //   console.log(e)
      //   // this.payShow = true;
      //   // this.goods = arr;
      // }).finally(() => {
      //   this.loading = false;
      // });       
    },
    onCanelPay() {
      // this.$router.push("/shop/orderlist");
    },
    onPwdFinish(v) {
      // console.log(v);
      this.loading = true;
      this.$http.post("order/confirmCart", {
        "paypasswd": v,
        "did": window.device.uuid,
        // "goods": this.goods
      }).then((res) => {
        //todo
        //成功跳转到支付成功页面
        //钱不足调到充值页面
        //是否提示对话框
        this.$router.push("/shop/success");
      }).catch((e)=> {
        let code = util.getErrorCode(e);
        let theCode = window.globalData.code;
        if(code == theCode.CART_COIN_NOT_ENOUGH) {
          this.$dialog.alert({
            message: '您的余额不足,请充值后到未支付订单页面完成支付'
          }).then(()=> {
            this.$router.push("/shop/orderlist");
          });
        }
        else if(code == theCode.PAYPASSWORD_IS_WRONG) {
          this.$dialog.alert({message: '密码错误'})
        }
        else if(code == theCode.CART_IS_EMPTY) {
          this.$dialog.alert({message: '购物车是空的'}) 
        }
        else {
          this.$dialog.alert({message: '操作失败'}) 
        }
      }).finally(()=>{
        this.loading = false;
      })      
    }
  },
}
</script>


