<template>
  <div class="c-pay-box">
    <div class="c-pay-num">{{num | toNumberString}} XToken</div>
    <!-- 密码输入框 -->
    <van-password-input
      :value="value"
    />
    <!-- 数字键盘 -->
    <van-number-keyboard class="c-pay-keyboard"
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
    />
  </div>
</template>
<script>
export default {
  name: 'pay',
  props: {
    num: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      value: '',
      showKeyboard: true
    };
  },

  methods: {
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);
      if(this.value.length >= 6) {
        let v = this.value;
        this.$emit("finish", v);
        this.value = "";
        //this.num = 0;
      }
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    }    
  },
  mounted () {
  }
}
</script>

<style scoped>
.c-pay-keyboard.van-number-keyboard{
  position: static !important;
}
.c-pay-num{
  text-align: center;
  font-weight: bold;
  margin-bottom: 12px;
  margin-top: 8px;
  font-size: 16px;
}
.c-pay-box >>> .van-password-input__security li{
  border-right: 1px solid #ccc;
}
.van-password-input__security{
  border: 1px solid #ccc;
}
.c-pay-box >>> .van-password-input__security li:last-child{
  border-right: none;
}
</style>
